.contenedorCardsGeneral {
  /* margin-top: 100px; */
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

/* -------------slider------------- */
.sliderPromocion {
  display: flex;
  background-image: url("../img/fondoSlider.jpg");
  background-size: cover;
  background-position: 100% 60%;
  filter: brightness(0.8);
  background-attachment: fixed;
  width: 100%;
  height: 50vh;
  margin: 0 0 2rem 0;
  /* position: relative; */
  box-shadow: 15px 15px 15px black;
}
.containergShopGlobal {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.titleStore {
  padding: 0.5rem;
  position: absolute;
  font-size: 6em;
  right: 14rem;
  top: 4rem;
  color: #fff;
  text-shadow: 1px 1px #fff;
  font-style: italic;
}

/* -------------filters------------- */

.filters {
  min-width: 20%;
  /* height: 100vh; */
  /* flex-grow: 1; */
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 1rem;
}

.input-text {
  margin: 2rem 0;
  width: 90%;
  height: 5vh;
  padding: 0.5rem;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #efeefe;
  font-size: 1.3em;
  color: white;
}

.form-control {
  width: 90%;
  height: 5vh;
  font-size: 1.3em;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #efeefe;
  color: white;
}
.form-control option {
  background-color: rgba(5, 4, 4, 0.973);
}

/* -------------card------------- */
.ContainerStore {
  min-width: 80%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  /* margin: auto; */
  transition: all 1s ease-in-out;
}
.container-cards {
  width: 100%;
  display: flex;
}
.lupaView {
  width: 2.5rem;
}

.cardProducto {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 2rem 2rem 2rem 2rem;
  width: 15em;
  height: 300px;
  color: rgb(161, 70, 70) 8px;
  border-radius: 5px;
  -webkit-box-shadow: inset -5px 17px 109px 8px rgba(59, 38, 38, 0.88);
  -moz-box-shadow: inset -5px 17px 109px 8px rgba(0, 0, 0, 0.88);
  box-shadow: inset -5px 17px 109px 8px rgba(49, 49, 49, 0.88);
  background-image: url(https://jberlife.com/wp-content/uploads/2021/04/GettyImages-1191995863-2-scaled.jpg);
}

.cardProducto > img {
  align-self: center;
  width: 55%;
  height: 8rem;
  margin-bottom: 0px;
  object-fit: scale-down;
}
.precio {
  color: blanchedalmond;
  background-color: rgba(133, 43, 43, 0.925);
  border: solid 1px rgba(168, 168, 168, 0.61);
  border-radius: 4px;
  text-align: center;
  position: relative;
  font-size: 0.8rem;
  width: 4.5rem;
  top: 2rem;
  left: 1rem;
  z-index: 100;
}

.flag {
  border-radius: 0%;
  text-align: center;
  position: relative;
  font-size: 0.8rem;
  width: 1.8rem;
  top: 0.2rem;
  left: 11.8rem;
  z-index: 101;
  object-fit: cover;
}
.txtCard {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 30vh;
  align-items: center;
  color: white;
  justify-content: center;
  margin: 0px;
}

.txtCard > h2 {
  display: flex;
  width: 100%;
  height: 3rem;
  align-items: center;
  justify-content: center;
  text-shadow: 1px 1px 1px rgb(0, 0, 0);
  padding: 0.1rem 1rem 0rem 1rem;
  background-color: rgba(105, 105, 105, 0.171);
  text-align: center;
  font-size: 1rem;
}

.textCenter {
  text-align: center;
  font-size: 2rem !important;
  text-decoration: none !important  ;
}

.categoryTxt {
  text-decoration: none !important;
}

.botonesShop {
  margin-top: 1rem;
  width: 90%;
  display: flex;
  justify-content: space-evenly;
}

.iconShop {
  align-self: center;
  cursor: pointer;
}

.nofound {
  width: 100%;
  margin-top: 15rem;
  height: 50vh;
  display: flex;
  color: white;
  font-size: 2.5rem;
  flex-direction: column;
  transition: all 1s ease-in-out;
}

.nofound-h2 {
  margin-top: 1.5rem;
  text-align: center;
}

.imagenVacia {
  align-self: center;
  width: 55%;
  height: 8rem;
  margin: auto;
  object-fit: scale-down;
}
.botonesCart * {
  color: black !important;
}

.link-carta {
  text-decoration: none !important;
}

.link-about1 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 2rem;
  color: #efefefe8;
  background-color: #f3a446;
  text-decoration: none;

  margin-bottom: 1rem;
  font-size: 1.35em;
  font-weight: 700;
}

@media only screen and (min-width: 0px) and (max-width: 1200px) {
  .filters {
    min-width: 100%;
    height: auto;
  }
  .ContainerStore {
    max-width: 90%;
  }
  .containergShopGlobal {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .nofound {
    margin-top: 0;
    font-size: 2rem;
  }
  .titleStore {
    display: none;
  }

  .sliderPromocion {
    display: flex;
    background-image: url("../img/fondoSlider.jpg");
    background-size: cover;
    background-position: center;
  }
}

@media only screen and (min-width: 1200px) and (max-width: 1325px) {
  .input-text,
  .form-control {
    width: 100%;
  }
  .filter {
    align-items: center;
  }
}

@media (max-width: 530px) {
  .link-about1 {
    font-size: 1em;
  }

  .filters {
    /* padding: 0 1.8rem; */
  }
}

@media (max-width: 360px) {
  .link-about1 {
    display: none;
  }
}
