* {
  font-family: "Roboto", sans-serif;
}

.navbar {
  background-color: transparent !important;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 1000;
  padding: 1rem;
  transition: all 1s;
  flex-wrap: nowrap;
}

.header {
  position: fixed;
  width: 100%;
  z-index: 500;
  transition: 0.3s ease-in;
  overflow: hidden;
}
.pruebaMenu {
  margin-right: 2rem !important;
  width: 20rem;
  display: flex;
  justify-content: center;
  padding: 5;
}

.header-bg {
  background-color: black;
}


.menuLinks {
  display: flex;
  margin-left: 2rem;
}

.linkGeneral {
  padding: 0.1rem 1rem;
  border-radius: 5px;
  color: #efefef;
  font-size: x-large;
  text-decoration: none;
  font-size: 0.5 !important;
}

.nav-linkUser {
  color: #000;
  text-decoration: none;
  padding: 1.5rem 1rem;
  font-weight: 800;
  text-align: center;
}

.container-nav-LinkUser {
  color: black !important;
  flex-direction: column;
  display: flex;
  width: 7rem;
}

.navLi {
  list-style-type: none !important;
  color: #000;
}

.linkGeneral:hover {
  color: #000;
  background-color: rgba(255, 255, 255, 0.281);
}

.linkAvatar {
  text-decoration: none;
  list-style-type: none !important;
  margin: 0;
  padding: 0;
}

.linkAvatar:hover {
  background-color: transparent !important;
}


.logo {
  width: 4rem;
  border-radius: 50%;
}

.linkLogo {
  font-family: "Roboto",  sans-serif;
  font-size: 2.5rem;
  color: #efefef;
  text-decoration: none;
  align-items: center;
  display: flex;
}

.linkLogo:hover {
  color: rgba(255, 255, 255, 0.37);
}

.spanUser {
  font-size: 1.2rem;
}

.carritoCompras{
  cursor: pointer;
}

.carritoCompras2{
  color: #000 !important;
}

.carritolink2{
  margin: auto 1rem auto 0;
}



.carritolink{
  text-decoration: none;
  color: #efeefeb2;
  margin: auto 0;
}
.carritolink:hover{
  color: #fff;
}


@media (max-width: 567px) {
  .navbar {
    padding: 0;
  }
  .navbarBackgound {
    padding: 0;
    background-color: #000 !important;
  }
  .linkLogo {
    font-size: 1.5rem !important;
  }
  .logo {
    width: 2rem;
  }
  .userImg {
    width: 4rem;
  }
  .spanUser {
    display: none;
  }
}
